import{useEffect,useRef}from'react';import{useActiveAccount,useSelectedAccount}from'@onekeyhq/kit/src/states/jotai/contexts/accountSelector';import{useDebounce}from"../../../hooks/useDebounce";export function useHandleDiscoveryAccountChanged({num,handleAccountChanged}){var{activeAccount}=useActiveAccount({num});var{selectedAccount}=useSelectedAccount({num});var debouncedActiveAccount=useDebounce(activeAccount,200);var debouncedSelectedAccount=useDebounce(selectedAccount,200);var activeAccountRef=useRef(activeAccount);var selectedAccountRef=useRef(selectedAccount);useEffect(function(){activeAccountRef.current=activeAccount;selectedAccountRef.current=selectedAccount;},[activeAccount,selectedAccount]);useEffect(function(){if(handleAccountChanged){if(debouncedActiveAccount.isOthersWallet&&debouncedActiveAccount.account?.id===debouncedSelectedAccount.othersWalletAccountId||debouncedActiveAccount.indexedAccount?.id===debouncedSelectedAccount.indexedAccountId){handleAccountChanged({activeAccount:activeAccountRef.current,selectedAccount:selectedAccountRef.current},num);}}},[debouncedActiveAccount,debouncedSelectedAccount,handleAccountChanged,num]);}